.landing {
  background: url('/assets/images/moses_mdluli-bg2.jpg')no-repeat center bottom /cover;
  color: #fff;
  margin: 0 auto;
  text-align: center;
  height: 100%;
  min-height: 100vh; //If Content is less than viewport else
  position: relative;

  &__video {
    position: absolute;
    left: 0;
    right: 0;
    overflow: auto;
    width: 100%;
  }
  &__intro {
    margin: 0 auto;
    position: relative;
    overflow: auto;
  }
  &__inner {
    display: inline-block;
    @media only screen and (min-width: 330px) {
      padding: 65% 0 0;
    }
    @media only screen and (min-width: 600px)  {
      padding: 40% 0 0;
    }
    @media only screen and (min-width: 1000px) {
      padding: 12em 0 10%
    }
  }
}
